.bar-wrapper {
    display: flex;
    flex-direction: row;
    border: 4px groove;
    border-radius: 10px;
    height: 30px;
    width: 100%;
    overflow: hidden;
    margin: 2px;
}

.bar-fill {
    background-color: rgb(245, 245, 245);
    width: 100%;
    height: 100%;
    transition: 1s;
}
