.selector-wrapper{
    display: flex;
    flex-direction: column;
    width: 100vw;
    min-height: 100vh;
    background: url("https://cdn1.bbcode0.com/uploads/2020/12/10/2a5a3faf2c4e934d771bd7ddcc3abb0b-full.jpg");
    background-size: 100%;
    color: white;
}

.sides-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.side {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.brawl-button {
    width: 20%;
    font-size: 30px;
    align-self: center;
    position: relative;
    align-self: flex-end;
    margin: 0 0 200px 0;
}


h1, h2, h3, h4 {
    margin: 5px; 
}